import React from 'react'
import { Router, Location } from '@reach/router';

import Home from '../_pages/home';
import People from '../_pages/people';
import Projects from '../_pages/projects';
import Project from '../_pages/project';
import CreateProject from '../_pages/projects/create';
import Profile from '../_pages/profile';


export default class Index extends React.Component {
  constructor(props) {
    super(props);
  }

  render() {
    return (
      <Location>
        {({ location }) => (
          <Router location={location}>
            <Home path="/" />
            
            <People path="/people" />
            <Profile path="/profile/:username" />
            <Profile path="/profile" />
            
            <Projects path="/projects" />
            <Project path="/project/:projectId" />
            <CreateProject path="/projects/create" />
          </Router>
        )}
      </Location>
    );
  }
}